.zero {
    color: green;
}

.moreThenFive {
    color: red;
}

.generalCounterMin {
    display: flex;
    justify-content: center;
    background-color:cyan;
    border: black solid;
    border-radius: 10px;
    width: 60px;
    margin: 0px 0px 20px 0px;
}

.generalCounterMax {
    display: flex;
    justify-content: center;
    background-color:cyan;
    border: red solid;
    border-radius: 10px;
    width: 60px;
    margin: 0px 0px 20px 0px;
}
.counterGeneral {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 400px;
    height: 300px;
    border: 2px solid darkblue;
    border-radius: 30px;
}
.counter {
    display: flex;
    justify-content: center;
    width: 50px;
}
.buttons {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 170px;
    height: 30px;
    border: 2px solid darkblue;
    border-radius: 30px;
}