.settings {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 400px;
  height: 300px;
  border: 2px solid darkblue;
  border-radius: 30px;
}
.values {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 2px solid darkblue;
  border-radius: 30px;
  width: 350px;
  height: 130px;
}
.maxStart {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.maxStart input {
  border: 2px solid darkblue;
  border-radius: 30px;
  width: 90px;
}